<script>
var echarts = require('echarts')
export default {
  name: 'productdetailspage',
  data() {
    return {
      note: {
        borderStyle: 'none',
        width: '100%'
      },
      code: '',
      acc: '',
      charts: '',
      tableData: [{}],
      indexData: {},
      chartData: [],
      timeData: []
    }
  },
  mounted() {
    this.code = this.$route.params.code
    this.acc = this.$route.params.acc
    console.log(this.acc)
    this.chart()
    this.getTableData()
  },
  methods: {
    goBackV () {
      this.$router.go(-1)
    },
    getTableData() {
      var url = this.COCKPIT.cockpitBaseURL + '/get_score/get_trade_history/' + this.code
      this.$http
        .get(url)
        .then(
          response => {
            this.tableData = response.body.data
            this.getParameterData()
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    getParameterData() {
      var url = this.COCKPIT.cockpitBaseURL + '/get_score/get_chart/' + this.acc
      this.$http
        .get(url)
        .then(
          response => {
            this.indexData = response.body.ratio
            this.chartData = []
            this.timeData = []
            for (var i = 0; i < response.body.chart.length; i++) {
              this.chartData.push(response.body.chart[i][0])
              this.timeData.push(response.body.chart[i][1])
              this.chart()
            }
          },
          response => {
            console.log('连接出错Klinep')
          }
        )
        .catch(function(response) {
          console.log('连接catch-Kline' + response)
        })
    },
    tableRowStyle ({ row, rowIndex }) {
      return 'background-color: transparent'
    },
    tableHeaderColor ({ row, column, rowIndex, columnIndex }) {
      if (rowIndex === 0) {
        return 'background-color: transparent;color: #cdc4ec;'
      }
    },
    showDetail(row) {
      this.indexData = row
    },
    showDetail2(row) {
      this.$router.push({ name: 'cockpitDetails', params: { code: row.acc_info } })
    },
    tableCellStyle () {
      return 'border-color: #4E5D90;'
    },
    chart() {
      var option = {
        grid:
          {
            left: '120px',
            right: '60px',
            top: '10%',
            height: '85%'

          },
        xAxis: {
          type: 'category',
          data: this.timeData,
          axisLine: {
            lineStyle: {
              color: '#ffffff'
            }
          }
        },
        yAxis: {
          offset: '0',
          type: 'value',
          min: 0,
          max: 3000000,
          splitNumber: 7,
          axisLine: {
            lineStyle: {
              color: '#ffffff'
            }
          },
          splitLine: {
            lineStyle: {
              color: '#36394F',
              type: 'dashed'
            }
          }
        },
        series: [{
          data: this.chartData,
          type: 'line',
          lineStyle: {
            color: '#b26dea'
          },
          areaStyle: {
            color: {
              type: 'linear',
              x: 0,
              y: 0,
              x2: 0,
              y2: 1,
              colorStops: [{
                offset: 0, color: '#6d4397'
              }, {
                offset: 1, color: '#111111'
              }],
              global: false //
            }
          }
        }]
      }
      if (this.charts) {
      } else {
        this.charts = echarts.init(this.$refs.chart)
      }
      this.charts.clear()
      this.charts.setOption(option)
    }
  }
}
</script>

